>\r\n        <input class=\"searchBar\" type=\"text\" v-model=\"keyword\" placeholder=\"Search\" onfocus=\"\"/>\r\n            <ul class=\"stocksList\">\r\n                <li class=\"stockListItem grow\" @click.stop.prevent=\"selectStock(stock)\" v-for=\"stock in filteredStocks\">\r\n                    {{stock.name}}\r\n                </li>\r\n            </ul>\r\n        </div>\r\n</template>\r\n\r\n<script>\r\n    import {EventBus} from 'main.js'\r\n\r\n    export default {\r\n        name: 'stocks-list',\r\n        data(){\r\n            return {\r\n                stocks: [],\r\n                keyword: ''\r\n            }\r\n        },\r\n        created () {\r\n            this.fetchStocks();\r\n        },\r\n        methods: {\r\n            fetchStocks(){\r\n                this.$http.get('http://localhost:8080/data/stocks?size=1000').then(function (response) {\r\n                    response.data._embedded.stocks\r\n                        .forEach(stock => this.stocks.push(stock));\r\n                    this.selectStock(this.stocks[0]);\r\n                }).catch(function (error) {\r\n                    console.log(error);\r\n                });\r\n            },\r\n            selectStock(stock){\r\n                this.$router.push('/stocks/'+stock.id);\r\n            }\r\n        },\r\n        computed: {\r\n            filteredStocks(){\r\n                return this.stocks.filter((stock) => {\r\n                    return stock.name.toLowerCase().includes(this.keyword.toLocaleLowerCase());\r\n                });\r\n            }\r\n        }\r\n    }\r\n</script>\r\n\r\n<style>\r\n\r\n    #stockListContainer {\r\n        margin: 5px 0px 5px 5px;\r\n        display: flex;\r\n        flex-direction: column;\r\n        flex: 0;\r\n    }\r\n\r\n    .searchBar {\r\n        border: none;\r\n        /* width: 100%; */\r\n        font-size: 130%;\r\n        outline-width: 0;\r\n        padding: 5% 5% 5% 30px;\r\n        display: flex;\r\n        margin: 4px;\r\n        background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.03) 100%);\r\n    }\r\n\r\n    .stocksList {\r\n        height: 100%;\r\n        margin-top: 10px;\r\n        padding-bottom: 10px;\r\n        padding-left: 30px;\r\n        overflow-x: hidden;\r\n        overflow-y: scroll;\r\n        display: flex;\r\n        flex-direction: column;\r\n\r\n    }\r\n\r\n    .shadow{\r\n        height: 100%;\r\n        box-shadow: inset 0px 40px 18px -20px rgb(244, 251, 253);\r\n    }\r\n\r\n    .stocksList::-webkit-scrollbar{\r\n        width: 0;\r\n    }\r\n\r\n    .shadow:after{\r\n        position: absolute; top: 0; bottom: 0; left: -15px; right: -15px;\r\n        box-shadow: inset 0px 40px 18px -20px rgb(0, 7, 253);\r\n    }\r\n\r\n    .stockListItem {\r\n        user-select: none;\r\n        padding: 5% 0 5%;\r\n        list-style-type: none;\r\n        font-size: 150%;\r\n    }\r\n\r\n    .grow {\r\n        transition: all .2s ease-in-out;\r\n    }\r\n\r\n    .grow:hover {\r\n        text-shadow: -3px 3px 3px rgba(1, 1, 1,.15);\r\n        cursor: pointer;\r\n        font-weight: bold;\r\n        transform: scale(1.1) translate(5%,0);\r\n    }\r\n\r\n</style>"],"sourceRoot":""}]);

